import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import Button from "./elements/ButtonNav";
import Logo from "./elements/Logo";
import { VscChevronRight } from "react-icons/vsc";
export default class Navigation extends Component {
  render() {
    return (
      <>
        <div className="navbar backdropNav">
          <NavLink to="/" exact>
            <Button text="home" />
          </NavLink>
          <NavLink to="/works">
            <Button text="works" />
          </NavLink>
          <NavLink to="/extra">
            <Button text="extra" />
          </NavLink>
        </div>
        <div className="nav backdropNav">
          <div className="logo">
            <Logo />
          </div>
          <div className="menu">
            <div className="menu-icon">
              <VscChevronRight />
            </div>
          </div>
        </div>
      </>
    );
  }
}
