import "./styles.css";
import React, { Component, useEffect } from "react";
import LocomotiveScroll from "locomotive-scroll";
import Home from "./components/Home";
import Extra from "./components/Extra";
import NavBar from "./components/Navigation";
import { BrowserRouter, Route, Switch } from "react-router-dom";
// require("./js.js");
const App = () => {
  // export default class App extends Component {
  const mainRef = React.createRef();
  useEffect(() => {
    const scroll = new LocomotiveScroll({
      el: mainRef.current,
      smooth: true,
      smartphone: { smooth: true },
      tablet: { smooth: true },
      lerp: 0.04,
      // multiplier: 1.8,
      // touchMultiplier: 1.4,
      repeat: true,
      offset: [0, "0%"],
      resetNativeScroll: false,
      gestureDirection: "both"
    });
    setTimeout(() => {
      processing();
    }, 4000);
    function processing() {
      var processing = [
        {
          offset: 0,
          background: "rgb(var(--color-one))"
        },
        {
          offset: 0.2,
          background: "rgb(var(--color-one))"
        },
        {
          offset: 0.25,
          background: "transparent",
          border: "1px solid rgb(var(--color-one))"
        },
        {
          offset: 0.5,
          background: "transparent",
          border: "1px solid transparent"
        },
        {
          offset: 0.85,
          background: "transparent",
          border: "1px solid rgb(var(--color-one))"
        },
        {
          offset: 0.9,
          background: "rgb(var(--color-one))"
        },
        {
          offset: 1,
          background: "rgb(var(--color-one))"
        }
      ];
      document.getElementById("logo1").animate(processing, {
        duration: 5000,
        iterations: Infinity,
        easing: "cubic-bezier(1, 0, 0.35, 1.08)",
        delay: 1000
      });
      document.getElementById("logo2").animate(processing, {
        duration: 5000,
        iterations: Infinity,
        easing: "cubic-bezier(1, 0, 0.35, 1.08)",
        delay: 1100
      });
      document.getElementById("logo3").animate(processing, {
        duration: 5000,
        iterations: Infinity,
        easing: "cubic-bezier(1, 0, 0.35, 1.08)",
        delay: 1200
      });

      var scaling = [
        {
          offset: 0
        },
        {
          offset: 1
        }
      ];
      document.getElementById("logospine").animate(scaling, {
        duration: 5000,
        iterations: Infinity,
        easing: "cubic-bezier(1, 0, 0.35, 1.08)",
        delay: 1200
      });
    }
    document.getElementById("color").addEventListener("input", (e) => {
      let one = e.target.value
        .match(/[A-Za-z0-9]{2}/g)
        .map((v) => parseInt(v, 16));
      document
        .querySelector(":root")
        .style.setProperty("--color-one", one.join(","));
      one[2] = one[2] - 85;
      document.querySelector(":root").style.setProperty("--text-color", one);
    });
  });

  return (
    <BrowserRouter>
      <NavBar />
      <input type="color" id="color" />
      <div ref={mainRef}>
        <Switch>
          <Route path="/" component={Home} exact />
          <Route path="/extra" component={Extra} />
          <Route path="/works" component={Extra} />
          {/* <Route component={Error} /> */}
        </Switch>
      </div>
    </BrowserRouter>
  );
};
export default App;
